import { JsonConvertableImpl, BpmnModelJsonHelper } from '@edp-aif/common-api';

export class FormEntityData extends JsonConvertableImpl {
    dataId: string;
    children: { [key: string]: Array<FormEntityData> } = {};

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.dataId = null;
        if (this.children != null) {
            for (var key in this.children) {
                delete this.children[key];
            }
        }
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "dataId", this.dataId);

        if (this.children != null) {
            let childrenObj = {};

            for (let key in this.children) {
                BpmnModelJsonHelper.SetAttributeValue(childrenObj, key, this.children[key]);
            }

            BpmnModelJsonHelper.SetAttributeValue(obj, "children", childrenObj);
        }

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.dataId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "dataId");

        let childrenObj: {} = BpmnModelJsonHelper.GetAttributeValue(obj, "children");
        if (childrenObj != null) {
            for (let key in childrenObj) {
                let value: Array<FormEntityData> = BpmnModelJsonHelper.GetAttributeValueAsList(childrenObj, key, FormEntityData);
                this.children[key] = value;
            }
        }
    }
}