import { Component, OnInit, Inject, LOCALE_ID } from '@angular/core';
import { BsModalRef } from '@farris/ui-modal';
import { Subject } from 'rxjs';
import { SELECTOR_ARGS_INJECTOR } from '../../config/selector-args-injector.config';
import { ISlimCreationRuleEntityEx } from '@edp-aif/common-api';
import { IAifCreationRuleSelectorComponent } from '../../base/IAifCreationRuleSelectorComponent';
import { FormGetCreationRuleEntityExArgs, GetCreationRuleEntityExArgs } from '../../form-args';
import { AifDataService } from '../../service/aif-data.service';

/**
 * @deprecated please use @edp-bif/runtime-api 
 */
@Component({
  selector: 'lib-push-selector',
  templateUrl: './push-selector.component.html',
  styleUrls: ['./push-selector.component.css'],
})
export class PushSelectorComponent implements OnInit, IAifCreationRuleSelectorComponent {

  //ç®æ VoIdåè¡¨
  targetVoIdList: string[] = [];

  //ç®æ VoIdä¸ç®æ VoNameæ å°
  targetVoNameDict: {
    [targetVoId: string]: string
  } = {};

  //ç®æ VoIdä¸ç¸åºççåè§ååè¡¨æ å°
  creationRuleDict: {
    [targetVoId: string]: ISlimCreationRuleEntityEx[]
  } = {};

  //éä¸­çç®æ VoId
  private _selectedTargetVoId: string;
  get selectedTargetVoId(): string {
    return this._selectedTargetVoId;
  }
  set selectedTargetVoId(targetVoId: string) {
    this._selectedTargetVoId = targetVoId;
    if (targetVoId != null) {
      this.selectedCreationRule = this.creationRuleDict[this._selectedTargetVoId][0];
    }
  }

  //éä¸­ççåè§å
  selectedCreationRule: ISlimCreationRuleEntityEx;

  bsModalRef4AifSelector: BsModalRef = null;
  subject4CreationRule: Subject<ISlimCreationRuleEntityEx> = new Subject();

  private args: FormGetCreationRuleEntityExArgs;
  constructor(
    @Inject(SELECTOR_ARGS_INJECTOR) args: GetCreationRuleEntityExArgs,
    private dataService: AifDataService,
    @Inject(LOCALE_ID) localeId: string,
  ) {
    this.args = new FormGetCreationRuleEntityExArgs();
    this.args.LoadFromJsonObject(args);
  }

  ngOnInit() {
    this.loadItems();
  }

  clickOk4AifSelector = () => {
    this.subject4CreationRule.next(this.selectedCreationRule);
    this.subject4CreationRule.complete();
    this.bsModalRef4AifSelector.close();
  };

  clickClose4AifSelector = () => {
    this.bsModalRef4AifSelector.close();
  };

  private loadItems(): void {
    let list$ = null;
    if (this.args.targetVoId == null) {
      if (this.args.bizFlowchartId == null) {
        list$ = this.dataService.getSlimCreationRulesBySourceVoId(this.args.sourceVoId);
      } else {
        list$ = this.dataService.getSlimCreationRulesBySourceVoIdAndFlowchartId(this.args.sourceVoId, this.args.bizFlowchartId);
      }
    } else {
      if (this.args.bizFlowchartId == null) {
        list$ = this.dataService.getSlimCreationRulesBySourceVoIdAndTargetVoId(this.args.sourceVoId, this.args.targetVoId);
      } else {
        list$ = this.dataService.getSlimCreationRulesBySourceVoIdAndTargetVoIdAndFlowchartId(this.args.sourceVoId, this.args.targetVoId, this.args.bizFlowchartId);
      }
    }
    list$.subscribe(
      (result: ISlimCreationRuleEntityEx[]) => {
        const list = result;
        list.forEach(elt => {
          if (this.targetVoIdList.includes(elt.targetVoId) === false) {
            this.targetVoIdList.push(elt.targetVoId);
            this.creationRuleDict[elt.targetVoId] = new Array<ISlimCreationRuleEntityEx>();
          }

          this.targetVoNameDict[elt.targetVoId] = elt.targetVoName;
          this.creationRuleDict[elt.targetVoId].push(elt);
        });
        this.selectedTargetVoId = this.targetVoIdList.length > 0 ? this.targetVoIdList[0] : null;
      }
    );
  }
}
