import { Component, OnInit, ViewChild, Inject, LOCALE_ID } from '@angular/core';
import { Subject } from 'rxjs';
import { BsModalRef } from '@farris/ui-modal';
import { TreeTableComponent } from '@farris/ui-treetable';
import { IAifDataSelectorComponent } from '../../base/IAifDataSelectorComponent';
import { Entity } from '@farris/devkit';

/**
 * @deprecated please use @edp-bif/runtime-api 
 */
@Component({
  selector: 'lib-source-data-selector',
  templateUrl: './source-data-selector.component.html',
  styleUrls: ['./source-data-selector.component.css'],
})
export class SourceDataSelectorComponent implements OnInit, IAifDataSelectorComponent {
  bsModalRef4AifSelector: BsModalRef;
  subject4EntityDatas: Subject<Entity[]> = new Subject();

  @ViewChild("treetable")
  treetable: TreeTableComponent;

  cols = [];
  treedata = [];

  private langCode: string;

  constructor(
    @Inject(LOCALE_ID) localeId: string,
  ) {

  }

  ngOnInit() {
  }

  clickOk4AifSelector = () => {
    this.subject4EntityDatas.next([]);
    this.subject4EntityDatas.complete();
    this.bsModalRef4AifSelector.close();
  };

  clickClose4AifSelector = () => {
    this.bsModalRef4AifSelector.close();
  };

}
