import { FormRestResult } from './FormRestResult';
import { BpmnModelJsonHelper } from '@edp-aif/common-api';

export class FormCreateResult extends FormRestResult {
    entityDataList: Array<string> = new Array();

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.entityDataList = new Array();
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "entityDataList", BpmnModelJsonHelper.StringListToJsonArray(this.entityDataList));

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.entityDataList = BpmnModelJsonHelper.StringListFromJsonArray(BpmnModelJsonHelper.GetAttributeValue(obj, "entityDataList"));
    }
}
