import { BpmnModelJsonHelper } from '@edp-aif/common-api';
import { FormRestArgs } from './FormRestArgs';

export class FormCreateArgs extends FormRestArgs {
    formVoId: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.formVoId = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "formVoId", this.formVoId);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.formVoId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "formVoId");
    }
}
