import { Injectable, Inject, LOCALE_ID, ComponentRef, ComponentFactory, Injector, ComponentFactoryResolver, NgModuleFactoryLoader, NgModuleFactory, ReflectiveInjector } from '@angular/core';
import { AifLocalePipe } from '../pipe/locale.pipe';
import { ModalOptions, BsModalService } from '@farris/ui-modal';
import { FormSettings, ISlimCreationRuleEntityEx } from '@edp-aif/common-api';
import { RpcCreationRuleExecutorResult } from '../../entity';
import { AIF_UISTATE_KEY_CREATION_RULE_RESULT } from '../config/aif-form-constants';
import { ReferSelectorComponent } from '../components/refer-selector/refer-selector.component';
import { FormGetCreationRuleEntityExArgs } from '../form-args';
import { IAifCreationRuleSelectorComponent, IAifCreationRuleSelectorModule, IAifSelectorComponent, IAifDataSelectorComponent, IAifDataSelectorModule } from '../base';
import { SELECTOR_ARGS_INJECTOR } from '../config/selector-args-injector.config';
import { AifDataService } from './aif-data.service';
import { FrameContext, Entity } from '@farris/devkit';
import { from, Observable } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { SourceDataSelectorComponent } from '../components/source-data-selector/source-data-selector.component';
import { AbstractAifUiService } from './abstract-aif-ui-service';

@Injectable()
export class AifTargetUiService extends AbstractAifUiService {
    private readonly aifUIStateCreationRuleResultKey: string = AIF_UISTATE_KEY_CREATION_RULE_RESULT;

    constructor(
        protected aifDataService: AifDataService,
        protected frameContext: FrameContext,
        protected modalService: BsModalService,
        protected injector: Injector,
        protected cfr: ComponentFactoryResolver,
        protected loader: NgModuleFactoryLoader,
        @Inject(LOCALE_ID) public localeId: string,
    ) {
        super(frameContext, modalService, injector, cfr, loader, localeId);
    }

    /**
     * åç§æ¶æå¼çåè§åéæ©å¨
     */
    public openCreationRuleSelector4Refer(targetVoId: string, bizFlowchartId?: string, modalOptions?: ModalOptions, customSelectorModuleUrl?: string): Observable<ISlimCreationRuleEntityEx> {
        const args = new FormGetCreationRuleEntityExArgs();
        args.targetVoId = targetVoId;
        args.bizFlowchartId = bizFlowchartId;

        return this.openCreationRuleSelector(args, ReferSelectorComponent, modalOptions, customSelectorModuleUrl);
    }

    /**
     * ç¹å®åç§æ¶æå¼çåè§åéæ©å¨
     */
    public openCreationRuleSelector4SpecificRefer(sourceVoId: string, targetVoId: string, bizFlowchartId?: string, modalOptions?: ModalOptions, customSelectorModuleUrl?: string): Observable<ISlimCreationRuleEntityEx> {
        const args = new FormGetCreationRuleEntityExArgs();
        args.sourceVoId = sourceVoId;
        args.targetVoId = targetVoId;
        args.bizFlowchartId = bizFlowchartId;

        return this.openCreationRuleSelector(args, ReferSelectorComponent, modalOptions, customSelectorModuleUrl);
    }

    /**
     * æå¼æ¥æºåæ®éæ©å¨
     * @param formSettings 
     * @param modalOptions 
     */
    public openSourceDataSelector4Refer(formSettings?: FormSettings, modalOptions?: ModalOptions): Observable<Entity[]> {
        modalOptions = modalOptions == null ? this.defaultModalOptions : modalOptions;
        modalOptions.title = modalOptions.title == null ? this.aifLocalePipe.transform("sourceDataSelector") : modalOptions.title;

        let cmpR: ComponentRef<IAifDataSelectorComponent> = null;
        if (formSettings == null || formSettings.sourceDataSelectorModuleUrl == null) {
            throw "ææ é»è®¤å®ç°ï¼è¯·ä½¿ç¨èªå®ä¹æ¥æºåæ®éæ©å¨";
            let cmpF: ComponentFactory<SourceDataSelectorComponent> = this.cfr.resolveComponentFactory(SourceDataSelectorComponent);
            let inj: Injector = Injector.create([
            ], this.injector);
            cmpR = cmpF.create(inj);
            this.configModalButtons(modalOptions, cmpR);

            cmpR.instance.bsModalRef4AifSelector = this.modalService.show(cmpR, modalOptions);
            return cmpR.instance.subject4EntityDatas.asObservable();
        } else {
            // return from(System.import(formSettings.sourceDataSelectorModuleUrl.substring(0, formSettings.sourceDataSelectorModuleUrl.indexOf("#")) + ".js")).pipe(
            return from(this.loader.load(formSettings.sourceDataSelectorModuleUrl)).pipe(
                switchMap(
                    (moduleFactory: NgModuleFactory<IAifDataSelectorModule>) => {
                        const moduleRef = moduleFactory.create(this.frameContext.root.injector);
                        cmpR = moduleRef.instance.createSourceDataSelector(this.frameContext.root.injector);
                        this.configModalButtons(modalOptions, cmpR);

                        cmpR.instance.bsModalRef4AifSelector = this.modalService.show(cmpR, modalOptions);
                        return cmpR.instance.subject4EntityDatas.asObservable();
                    }
                )
            );
        }
    }

    /**
     * è·åä¼ å°æ ¹ç»ä»¶UIStateä¸ççåè§åæ§è¡å¨ç»æ
     */
    public getRpcCreationRuleExecutorResult(): RpcCreationRuleExecutorResult {
        const temp = this.frameContext.root.uiState[this.aifUIStateCreationRuleResultKey];
        const result = new RpcCreationRuleExecutorResult();
        if (typeof temp == "string") {
            result.LoadFromJson(temp);
        } else if (typeof temp == "object") {
            result.LoadFromJsonObject(temp);
        } else {
            throw "ä¸åæ³çç±»å";
        }
        return result;
    }
}