    /**
     * è·åè¡¨åéç½®å®ä½
     * @param formSettingsId 
     */
    public getFormSettingsById(formSettingsId: string): Observable<FormSettings> {
        if (formSettingsId == null || formSettingsId.trim() == "") {
            throw "çåè§åä¸ºç©º";
        }
        const formSettings$: Observable<string> = this.repository.proxy.request(`${this.generalTextUrl}/${formSettingsId}`, this.generalTextMethod, null, { responseType: "text" });
        return formSettings$.pipe(
            map(
                (data: string) => {
                    const generateText = new GeneralTextEntity();
                    generateText.LoadFromJson(data);
                    const formSettings = new FormSettings();
                    formSettings.LoadFromJson(generateText.text);
                    return formSettings;
                }
            )
        );
    }

