import { FrameContext } from '@farris/devkit';
import { BsModalService, ModalOptions } from '@farris/ui-modal';
import { Injector, ComponentFactoryResolver, NgModuleFactoryLoader, Inject, LOCALE_ID, ComponentRef, ComponentFactory, ReflectiveInjector, NgModuleFactory, Type } from '@angular/core';
import { AifLocalePipe } from '../pipe/locale.pipe';
import { FormGetCreationRuleEntityExArgs } from '../form-args';
import { Observable, from } from 'rxjs';
import { ISlimCreationRuleEntityEx } from '@edp-aif/common-api';
import { IAifCreationRuleSelectorComponent, IAifCreationRuleSelectorModule, IAifSelectorComponent } from '../base';
import { SELECTOR_ARGS_INJECTOR } from '../config/selector-args-injector.config';
import { switchMap } from 'rxjs/operators';

export abstract class AbstractAifUiService {
    /**
     * åç½®æ¨¡ææ¡éç½®
     */
    protected readonly defaultModalOptions: ModalOptions = {
        title: null,
        width: 650,
        height: 415,
        buttons: null,
        showButtons: null,
    };

    protected aifLocalePipe: AifLocalePipe;

    constructor(
        protected frameContext: FrameContext,
        protected modalService: BsModalService,
        protected injector: Injector,
        protected cfr: ComponentFactoryResolver,
        protected loader: NgModuleFactoryLoader,
        @Inject(LOCALE_ID) protected localeId: string,
    ) {
        this.aifLocalePipe = new AifLocalePipe(localeId);
    }

    protected openCreationRuleSelector(args: FormGetCreationRuleEntityExArgs, type: Type<IAifCreationRuleSelectorComponent>, modalOptions?: ModalOptions, customSelectorModuleUrl?: string): Observable<ISlimCreationRuleEntityEx> {
        if (typeof modalOptions == "string") {
            if (modalOptions == "") {
                throw "modalOptionsä¸å¯ä¸ºç©º";
            }
            modalOptions = JSON.parse(modalOptions);
        }

        modalOptions = modalOptions == null ? this.defaultModalOptions : modalOptions;
        modalOptions.title = modalOptions.title == null ? this.aifLocalePipe.transform("creationRuleSelector") : modalOptions.title;

        let cmpR: ComponentRef<IAifCreationRuleSelectorComponent> = null;
        if (customSelectorModuleUrl == null || customSelectorModuleUrl.trim() == "") {
            let cmpF: ComponentFactory<IAifCreationRuleSelectorComponent> = this.cfr.resolveComponentFactory(type);

            let inj: Injector = ReflectiveInjector.resolveAndCreate([
                { provide: SELECTOR_ARGS_INJECTOR, useValue: args.ConvertToJsonObject() },
            ], this.injector);
            cmpR = cmpF.create(inj);
            this.configModalButtons(modalOptions, cmpR);

            cmpR.instance.bsModalRef4AifSelector = this.modalService.show(cmpR, modalOptions);
            return cmpR.instance.subject4CreationRule.asObservable();
        } else {
            return from(this.loader.load(customSelectorModuleUrl)).pipe(
                switchMap(
                    (moduleFactory: NgModuleFactory<IAifCreationRuleSelectorModule>) => {
                        const moduleRef = moduleFactory.create(this.frameContext.root.injector);
                        cmpR = moduleRef.instance.createCreationRuleSelector(this.frameContext.root.injector);
                        this.configModalButtons(modalOptions, cmpR);
                        cmpR.instance.bsModalRef4AifSelector = this.modalService.show(cmpR, modalOptions);
                        return cmpR.instance.subject4CreationRule.asObservable();
                    }
                )
            );
        }
    }

    /**
     * éç½®æ¨¡ææ¡æé®äºä»¶
     * @param modalOptions 
     * @param cmpR 
     */
    protected configModalButtons(modalOptions: ModalOptions, cmpR: ComponentRef<IAifSelectorComponent>) {
        modalOptions.buttons = [
            {
                text: this.aifLocalePipe.transform("confirm"),
                cls: "btn btn-primary",
                handle: cmpR.instance.clickOk4AifSelector,
            },
            {
                text: this.aifLocalePipe.transform("cancel"),
                cls: "btn btn-secondary",
                handle: cmpR.instance.clickClose4AifSelector,
            }
        ];
        modalOptions.showButtons = true;
    }
}