import { Pipe, PipeTransform, Inject, LOCALE_ID } from '@angular/core';
import { LANG_RESOURCES } from '../i18n/lang.resource';

/**
 * @deprecated please use @edp-bif/runtime-api 
 */
@Pipe({ name: 'aifLocale' })
export class AifLocalePipe implements PipeTransform {
    private langCode: string;
    private lang: {};
    constructor(
        @Inject(LOCALE_ID) localeId: string
    ) {
        this.langCode = localeId || "zh-CHS";
        this.lang = LANG_RESOURCES[this.langCode];
    }
    transform(name: string): any {
        return this.lang[name] || name;
    }
}

